*
{
    margin:0;
    padding:0;
}
.home
{
    
    max-width:1000px;
    margin:0 auto;
    margin-top:90px;
    height:100%;
}
.home-title
{
    color: whitesmoke;
    font-size:30px;
    font-weight: 600;
    font-family:Arial, Helvetica, sans-serif;
   
}
.home-container
{
   position: absolute;
   top: 15%;
   left: 33%;

}
.home-form-container {
    box-shadow:1px 4px 12px -2px rgba(0,0,0,4.3);
    background:#3079ef;
    padding:30px 30px;
    border-radius:10px;
    width: 400px;
}
.home-form-container input{

    border:1px solid #e5e5e5;
    outline:none;
    padding:15px 10px;
    width:90%;
    border-radius:8px;
    margin:10px 0;
    background:transparent;
    color:white;
    font-size:14px;
}
.home-form-container input::placeholder
{
    color:rgb(245, 241, 241);
    font-size: 15px;
}
.home-form-container button{
    background: transparent;
border:1px solid white;
outline:none;
font-size:18px;
color:white;
padding:10px 40px;
margin-top:20px;
border-radius:12px;
cursor:pointer;
margin-left: 100px;
}